import { Container, Text, Title } from '@mantine/core';
import { MultiSelect } from '@mantine/core';
import React from 'react';
import { useState, useEffect, Component } from 'react';

class Verb {
    constructor(spanish, english) {
        this.value = spanish;
        this.spanish = spanish;
        this.english = english;
    }

    o(who) {
        if (this.spanish.endsWith("ar")) {
            switch(who) {
            case "yo":
                return`${this.spanish.substring(0, this.spanish.length - 3)}o`;
                break;
            case "tu":
                return `${this.spanish.substring(0, this.spanish.length - 3)}as`;
                break;
            default:
                return `unknown 'o' who: ${who}`;
            }
        } else if (this.spanish.endsWith("er") || this.spanish.endsWith("er")) {
            switch(who) {
            case "yo":
                return`${this.spanish.substring(0, this.spanish.length - 3)}o`;
                break;
            case "tu":
                return `${this.spanish.substring(0, this.spanish.length - 3)}as`;
                break;
            default:
                return `unknown 'o' who: ${who}`;
            }
        } else {
            return `unknown 'o' verb ending: ${this.spanish}`;
        }
    }
}

const Verbs = [
    new Verb("caminiar", "walk"),
    new Verb("trabajar", "work"),
    new Verb("esperar", "wait"),
];


export function VerbsContent() {
    const [value, setValue] = useState([]);

    useEffect(() => {
        console.log(`???? verbs=${value}`);
    });

    return (
        <Container>
            <MultiSelect
                data={Verbs.map(v => v.spanish)}
                label="Subjects"
                placeholder="Pick all that you like"
                value={value}
                onChange={setValue}
                clearable
                clearButtonLabel="Clear selection"
            />
        </Container>
    )
}
